.navbar-wrapper{
    position: relative;
    top: 0;
    width: 100%;
    z-index: 20000;
    display: grid;
    grid-template-columns: 50px 1fr;
    padding: 20px 50px;
    background: white;
}

.navbar-wrapper .nav-left{
    font-size: 20px;
    color: white;
    font-weight: 700;
    align-self: center;
}

.navbar-wrapper .nav-right{
    color: #2C3E50;
    justify-self: end;
    align-self: center;
    font-weight: 500;
}

.navbar-wrapper .nav-right a{
    color: black;
    margin: 0 20px;
}

.navbar-wrapper .nav-right .sign-up{
    color: white;
    padding: 5px 10px;
    border: 1px solid white;
    border-radius: 5px;
}


